
.banner {
  height: 3em;
  width: 100%;
  font-size: 18px;
  position: relative;
  z-index: 9999;
}
.banner div{
  background-color: #008BED;
  color: #ffffff;
  height: 3em;
  width: 100%;
  line-height:3em;
  padding: 0px 20px;
  position: fixed;
}
.banner span {
  padding: 0 5px;
}
.vue-content .vue-headline {
  position: relative;
}
.vue-content img[alt='标题图片']{
  height: 200px;
  width: 100%;
}
.vue-content cite {
  font-size: 1.7em;
  color: black;
  position: absolute;
  bottom: 10px;
  padding: 20px 14px;
  line-height: 1.2em;
}
/**
 * change the style of news.body
 */
.vue-content div.img-place-holder {
  height: 0;
}



